


















































































































































































@import "~@/css/vars";
.ui-container {
  pointer-events: none;
}
.strokes {
  display: flex;
  align-items: center;
  flex: 1 1;
  a {
    margin: 0.1rem;
    display: inline-flex;
  }
}
.enabled {
  display: flex;
  align-items: center;
  background-color: #f0f0f0;
  border-radius: 8px;
  padding: 0.5rem;
}

.color-icon {
  height: 20px;
  margin: 0.1rem;
}

.hidden {
  display: none;
}

.tool-icon-container {
  display: flex;
  align-items: center;
}

.tool-selection-container {
  display: flex;
  flex-direction: row;

  .tool-icon-container {
    // padding: 0 0rem 0 0.2rem;
  }
}

.background-disable {
  background-color: #f0f0f0;
  border: 0px solid #f0f0f0;
}

.background-enable {
  background-color: #000;
  border: 0px solid #000;
  color: white;
}

.tool-list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: 0;
  padding: 0;
  justify-content: center;
  align-items: flex-end;
  .tool-icon {
    transition: 0.1s;
    max-width: 100%;
    height: 4rem;
    width: 4rem;
    text-align: center;
    border-radius: 100%;
    position: relative;
    border-radius: 50%;
  }
  .stroke-size-tool {
    transition: 0.1s;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: stretch;
    border-radius: 100%;
    height: 4rem;
    width: 4rem;
    margin: 0;
    padding: 0;
  }
  .toolIconEnabled {
    box-shadow: 0px 0px 5px;
  }
  .tool-list-item {
    margin: 0.5rem;
  }
}
